<script>
import DataList from "@/components/datalist";

export default {
    name: "page-orders-list",
    components: { DataList },
    data() {
        return {
            btnFlag: false,
            layer: false,
            loading: false,
            deliverStatusList: [
                { value: 0, text: "" },
                { value: 1, text: "" },
                { value: 2, text: "" }
            ],
            orderQty: "",
            deliveryOrderQty: "",
            orderStatusList: [
                { value: 0, text: "" },
                { value: 1, text: "" },
                { value: 2, text: "" },
                { value: 3, text: "" },
                { value: 4, text: "" },
                { value: 5, text: "" },
                { value: 6, text: "" },
                { value: 7, text: "" },
                { value: 8, text: "" },
                { value: 9, text: "" },
                { value: 10, text: "" },
                { value: 11, text: "" },
                { value: 12, text: "" },
                { value: 13, text: "" },
                { value: 14, text: "" }
            ],
            newOrderStatusList: [
                { value: 0, text: "" },
                { value: 1, text: "" },
                { value: 2, text: "" },
                { value: 3, text: "" },
                { value: 4, text: "" },
                { value: 5, text: "" },
                { value: 6, text: "" },
                { value: 7, text: "" },
                // { value: 8, text: "" },
                { value: 9, text: "" },
                // { value: 10, text: "" },
                // { value: 11, text: "" },
                // { value: 12, text: "" },
                // { value: 13, text: "" },
                { value: 14, text: "" }
            ],
            filter: {
                orderNo: "",
                orderStatus: -1,
                deliverStatus: -1,
                startDate: "",
                endDate: "",
                demandName: ""
            },
            info: {},
            // orderStatusList: {},
            arr: [
                {
                    name: "",
                    xinhao: "",
                    guige: "",
                    num: "",
                    biaozhun: "",
                    lianjiek: "",
                    jiaodu: "",
                    zhongxinju: "",
                    xinshi: "",
                    length: "",
                    tuhao: "",
                    jinniuju: "",
                    guodupan: "",
                    jishuxinhao: ""
                }
            ],
            //
            preCheckMaterList: [],
            accessoryList: [],
            checkStatusList: [
                { value: 1, text: "" },
                { value: 2, text: "" },
                { value: 3, text: "" }
            ],
            fileDetailList: [],
            processList: [],
            statusList: {
                0: "",
                1: "",
                3: "",
                4: ""
            }
        };
    },
    created() {
        this.getOrderQty();
        // this.getOrderStatusList();
    },
    // activated(){}
    methods: {
        flush() {
            this.filter = {
                orderNo: "",
                orderStatus: -1,
                deliverStatus: -1,
                startDate: "",
                endDate: "",
                demandName: ""
            };
            this.$refs.list.update(true);
            this.getOrderQty();
        },
        getAccessory(orderNo) {
            this.$http
                .get(
                    `/haolifa/flowInstance/flow/accessoryInfo?formNo=${orderNo}&formId=0`
                )
                .then(res => {
                    res.forEach(item => {
                        if (item.fileUrl != "") {
                            this.accessoryList.push(item);
                        }
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getOrderStatusList() {
            this.$http
                .get("/haolifa/order-product/order-status-list")
                .then(res => {
                    this.orderStatusList = res.map(item => {
                        return { value: item.code, text: item.desc };
                    });
                });
        },
        getPreCheckMater(orderNo) {
            this.btnFlag = true;
            //
            this.$http
                .get(`/haolifa/order-product/order-material?orderNo=${orderNo}`)
                .then(res => {
                    this.preCheckMaterList = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        progress(item) {
            let id = "";
            this.$http
                .post("/haolifa/flowInstance/create", {
                    flowId: 1,
                    formId: item.id,
                    formType: 1,
                    formNo: item.orderNo,
                    summary: ""
                })
                .then(res => {
                    id = res.instanceId;
                    // this.loading = false;
                    // this.$toast(`,ID: ${res.instanceId}`);
                    this.$http
                        .post("/haolifa/order-product/updateStatus", {
                            orderNo: item.orderNo,
                            status: 1
                        })
                        .then(res => {
                            this.loading = false;
                            this.$toast(`,ID: ${id}`);
                        });
                });
        },
        // 
        approveProgress(item) {
            this.$router.push(
                `/order/approveProgress?formNo=${item.orderNo}&formId=0`
            );
        },
        infoShow(item) {
            // this.$router.push(`/order/info?orderNo=${item.orderNo}`);
            this.layer = true;
            this.getInfo(item.orderNo);
            this.getAccessory(item.orderNo);
            this.getProcess(item);
            // this.getOrderStatusList();
            this.$http
                .get(`/haolifa/order-product/accessory?orderNo=${item.orderNo}`)
                .then(res => {
                    this.fileDetailList = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getProcess(item) {
            this.$http
                .get(
                    `/haolifa/flowInstance/flow/progress/?formNo=${
                        item.orderNo
                    }`
                )
                .then(res => {
                    this.processList = [];
                    res.map(item => {
                        this.processList.push(item);
                        if (item.child.length > 0) {
                            item.child.map(it => {
                                this.processList.push(it);
                            });
                        }
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getInfo(orderNo) {
            this.$http
                .get(`/haolifa/order-product/details?orderNo=${orderNo}`)
                .then(res => {
                    this.info = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.layer = false;
            this.accessoryList = [];
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br>${item.deliveryNo}`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .delete(`/haolifa/order-product/delete/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg);
                        });
                }
            });
        },
        getOrderQty() {
            this.$http
                .post("/haolifa/statistics/order-product", this.filter)
                .then(res => {
                    this.orderQty = res.orderQty;
                    this.deliveryOrderQty = res.deliveryOrderQty;
                })
                .catch(e => {
                    this.$toast(e.msg);
                });
        }
    }
};
</script>

